/* CSS for gLobal.js components */
.Global {
    padding: 10px;
    margin: 20px 50px 50px 50px;
}

/* CSS for mainj.js */
/* Center the book */
.panel__image.panel__image--book {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 60px;
  
}

.article__content a:not(.btn):not(.toolbar-item) {
color: #006fc6;
}

.product-panel--book .panel__image--book img {
width: 100% !important;
}

.books__book__img {
width: 100%;
max-width: 20px;
will-change: transform;
transform-origin: 0 100%;
transform: rotate(-10deg);
transition: transform 0.3s ease-out;
}

.books__book__img:hover {
  transform: rotate(0);
}

.author__image-wrapper::before,
.books__book__image::before {
  display: block;
  content: "";
  height: 20%;
  width: 45%;
  position: absolute;
  background-image: url(https://i.imgur.com/5udcDlp.png);
  background-size: 100% auto;
  background-repeat: no-repeat;
  top: 84%;
  left: 35%;
  right: 0;
  transition: all 0.2s ease-out;
  transform-origin: 30% 50%;
  pointer-events: none;
}

.books__book__image:hover {
  position: relative;
}

#welcome{
  float: right;
  padding-top: 150px;
}

#search{
  float: right;
}

/* CSS for book.js components */

#book-card {
    cursor: pointer;
    text-align: center;
    margin: 30px auto;
    width: 220px;
    height: auto;
    border-radius: 40px;
    box-shadow: -2px 8px 17px 3px rgba(122,99,122,1);
    cursor: pointer;
    transition: 0.4s;
}

#book-img{
  width: 220px;
  height: auto;
  padding: 1px;
  border-radius: 40px;
  object-fit: cover;
  
}

#book-title {
    color: #123C69;
    overflow-wrap: break-word;
    font-family: 'Helvetica, sans-serif';
    font-weight: bolder;
    padding-top: 2px;
}

#book-card #book-img {
  width: inherit;
  height: inherit;
  border-radius: 40px;
  object-fit: cover;
}


#book-card:hover {
  transform: scale(0.9, 0.9);
  box-shadow: 5px 5px 30px 15px rgba(0,0,0,0.25), 
    -5px -5px 30px 15px rgba(0,0,0,0.22);
}